import {playMode} from 'common/js/config'
import {localSearch} from 'common/js/cache'

const state = {
  singer: {},
  playing: false,
  fullScreen: false,
  playlist: [],
  sequenceList: [],
  mode: playMode.sequence,
  currentIndex: -1,
  disc: {},
  topList: {},
  searchHistory: localSearch(),
  playHistory: [],
  favoriteList: []
}

export default state


